.where-buy-shops {
	padding-top: 100px;
	padding-bottom: 90px;

	&__title {
		margin-bottom: 26px;
		color: $color-red;
		text-transform: uppercase;
	}

	&__grid {
		display: grid;
		grid-template: 214px / repeat(5, 1fr);
		grid-auto-rows: 214px;
		margin-bottom: 90px;

		&:not(:last-child) {
			margin-bottom: 90px;
		}
	}
}
